angular.module("angular-input-utilities").run(["$templateCache", function($templateCache) {$templateCache.put("src/checkbox-list/checkbox-list-tpl.html","<div class=\"checkbox-list\">\n    <div ng-if=\"checkboxList.isHierarchical\" style=\"font-size: 13px\" class=\"hide-on-disabled\">(Restrictions are listed in hierarchical order. If a lower level restriction is selected, all higher level restrictions must also be selected.)</div>\n    <div class=\"checkbox\" ng-repeat=\"item in checkboxList.items track by $index\">\n        <input\n            id=\"restriction-{{$parent.$index+\'-\'+$index}}\"\n            name=\"restriction-{{$index}}\"\n            type=\"checkbox\" ng-change=\"(checkboxList.isHierarchical) ? checkboxList.hierarchyCheck(item.selected, $index) : \'\'\"\n            ng-model=\"item.selected\">\n        <label>{{item.description}}</label>\n    </div>\n\n    <!--hidden input for validation purposes-->\n    <select class=\"validation-checkbox\"\n            name=\"field_validation\"\n            required\n            title=\"(Check at least one box.)\"\n            ng-show=\"checkboxList.validationChecker()\"\n            ng-options=\"item.id as item.description for item in [{id: 1, description: \'\'}]\"\n            ng-model=\"checkboxList.valid\"></select>\n</div>\n");
$templateCache.put("src/multi-select-row/multi-select-row-tpl.html","<div class=\"row multi-select-row\">\n    <div class=\"col-xs-10 row-item-data\">\n        <div class=\"heading-6\" style=\"margin-bottom: 5px\">{{multiSelectRow.rowTitle}}</div>\n        <p ng-repeat=\"selectedOption in multiSelectRow.selectedOptions\">\n            <close-icon id=\"removeMultiSelectionItem-{{$index}}\" class=\"close-icon\"\n                        ng-click=\"multiSelectRow.removeOptionFromSelectList(selectedOption.id)\"></close-icon>\n            <span id=\"multiSelectedItemsText-{{$index}}\" class=\"row-item-data-text\">{{multiSelectRow.getSelectedOptionText(selectedOption.id)}}</span>\n        </p>\n        <p class=\"noneSelected\" ng-if=\"multiSelectRow.selectedOptions.length == 0\">\n            <strong>None selected</strong>\n        </p>\n    </div>\n    <div class=\"col-xs-2 multi-select-col\">\n        <div class=\"multi-select-group\">\n            <div class=\"multi-select-label\">{{multiSelectRow.multiSelectTitle}}</div>\n            <div ng-dropdown-multiselect\n                 ng-click=\"multiSelectRow.goToOptions()\"\n                 options=\"multiSelectRow.options\"\n                 events = \"multiSelectRow.events\"\n                 selected-model=\"multiSelectRow.selectedOptions\"\n                 extra-settings=\"multiSelectRow.extraSettings\"\n                 translation-texts=\"multiSelectRow.translationTexts\"></div>\n        </div>\n    </div>\n</div>\n");
$templateCache.put("src/saveButton/save-button-tpl.html","<span class=\"save-btn\">\n    <button class=\"btn btn-with-icon\" id=\"the-save-btn\"\n            type=\"submit\"\n            ng-click=\"saveButton.saveFn()\"\n            ng-class=\"{ \'btn-success\' : !saveButton.savingChangesTrigger && !saveButton.unsavedChangesTrigger,\n                        \'btn-warning\' : saveButton.unsavedChangesTrigger && !saveButton.savingChangesTrigger,\n                        \'btn-danger-hover\' : saveButton.savingChangesTrigger}\"\n            ng-disabled=\"!saveButton.unsavedChangesTrigger\">\n\n        <!--SAVING-->\n        <div ng-if=\"saveButton.savingChangesTrigger\">\n            <div class=\"relative-position\" ><util-spinner class-name=\"btn-loading-beacon\"></util-spinner></div>\n            <div class=\"margin-left-20\">Saving Changes</div>\n        </div>\n\n        <!--ALL CHANGES UP TO DATE-->\n        <span ng-if=\"!saveButton.savingChangesTrigger && !saveButton.unsavedChangesTrigger\"><i class=\"lds-icon lds-icon-check\"></i> Changes are up to date</span>\n\n        <!--UNSAVED CHANGES-->\n        <span ng-if=\"saveButton.unsavedChangesTrigger && !saveButton.savingChangesTrigger\"><i class=\"lds-icon lds-icon-warning\"></i>Save Changes</span>\n    </button>\n</span>\n");}]);